import classes from './Post.module.css';

const Post = (props) => {
  return (
    <div>
      <div className={classes.whoposts}>
        <img
          src='data:image/png;base64,'></img>
      </div>
      <div className={classes.Post}>
        {props.message}
        {props.imgUrl &&
        <div>
          <img src={props.imgUrl}/>
        </div>}
      </div>
      <div>
        <span className={classes.like}>
					like {props.likesCount}
        </span>
      </div>
    </div>
  );
}

export default Post;